* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  text-decoration: none;
  font-family: sans-serif;
  font-weight: lighter;
  box-sizing: border-box;
}
/* date entry options - https://stackoverflow.com/questions/14946091/are-there-any-style-options-for-the-html5-date-picker */
::-webkit-inner-spin-button{
    display: none;
}
.nav{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    width: 100%;
    /* z-index: 50; */
    height: 250px;
    font-size: 24px;

}
.nav h1{
    /* width: 50%; */
    padding: 20px;
    text-align: center;
    margin: auto 0;
    color: rgb(5, 11, 22);
}
.logoNoBack{
    height: 150px;
    width: 150px;
}

.nav-menus{ 
    /* width: 50%; */
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
}
.nav-menus li{
    padding: 0;
    margin: 0 30px 0 20px;
}

@media (max-width: 675px) and (min-width: 0px){
    .nav{
        height: 300px;
    }
    .nav-menus{
        font-size: 20px;
        /* height: 900px; */
    }
    
}

.header {
    background-image: url(/static/media/stock2.5e63c91e.png);
    box-shadow: inset 0 0 0 1000px rgba(1, 1, 1, 0.4);
    color: rgba(255, 255, 255, 1);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 300px; 
}

.header-theme {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

